.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  overflow: auto;

  &__top {
    margin-bottom: 32px;
    position: sticky;
    top: 0;
    z-index: 1;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    letter-spacing: 2px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 4px;
    border-bottom: 1px solid $main-txt-col;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    letter-spacing: 2px;
  }
}
